import React from "react";
import {firebase} from './firebase/firebaseConfig';

const Header = () =>{
    const signOut = () =>{
        firebase.auth().signOut();
    }
    const _stylesHeader = {
        header:{
            display: 'flex',
            height: '54px',
            width: '100%',
            borderBottom: '1px solid black'
        },
        caja1:{
            display: 'flex',
            textAlign: 'center',
            width: '45%',
            border: '1px solid black',
            justifyContent: 'center',
            alignItems: 'center'

        },
        caja2:{
            display: 'flex',textAlign: 'center',
            alignText: 'center',
            width: '15%',
            border: '1px solid black',
            justifyContent: 'center',
            alignItems: 'center'

        },
        caja3:{
            display: 'flex',textAlign: 'center',
            width: '40%',
            border: '1px solid black',
            justifyContent: 'center',
            alignItems: 'center'

        }
    }
    return(
 
 <header style={_stylesHeader.header}>
         <div style={_stylesHeader.caja1}>
            Logo
         </div>
         <div style={_stylesHeader.caja2}>
            Search
         </div>
         <div style={_stylesHeader.caja3}>
            Botones
            <button onClick={signOut}>Sign Out</button>
         </div>
         
     </header>
        
     
    );


}
export default Header;